import { mapGetters } from 'vuex';
import ImageMultipleIcon from 'vue-material-design-icons/ImageMultiple.vue';
import { generateUrl } from '@nextcloud/router';
import AbortControllerMixin from '../mixins/AbortControllerMixin.js';
export default {
  name: 'TagCover',
  components: {
    ImageMultipleIcon
  },
  mixins: [AbortControllerMixin],
  props: {
    tag: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      loadCover: false,
      observer: null
    };
  },
  computed: {
    // global lists
    ...mapGetters(['files', 'tags']),
    /**
     * @return {string}
     */
    coverUrl() {
      if (!this.loadCover) {
        return '';
      }
      return generateUrl(`/core/preview?fileId=${this.tag.referenceFileid}&x=${512}&y=${512}&forceIcon=0&a=1`);
    },
    count() {
      return this.tag.filesAssigned;
    }
  },
  watch: {
    loadCover() {
      if (this.tag.filesAssigned) {
        return;
      }
      this.$store.dispatch('fetchTagFiles', {
        id: this.tag.id,
        signal: this.abortController.signal
      });
    }
  },
  mounted() {
    this.observer = new IntersectionObserver(entries => {
      if (entries[0].isIntersecting) {
        this.loadCover = true;
        this.observer.disconnect();
      }
    });
    this.observer.observe(this.$el);
  }
};